.contact-section {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f1f3f5;
}
.contact-container {
  width: 100%;
  margin: 1rem 0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  background-color: #fff;
}
.contact-row {
  border: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.contact-row label {
  padding: 0.5rem 0.25rem;
  width: 100%;
  font-size: 1rem;
  font-weight: bold;
}
.contact-row input,
.contact-row textarea {
  font-size: 0.85rem;
}
.form-row textarea {
  width: 100%;
  height: 150px;
  padding: 0.5rem;
  font-size: 0.95rem;
  outline: none;
  border-radius: 0.25rem;
}

@media screen and (max-width: 767px) {
  .contact-container {
    max-width: 400px;
    min-height: 650px;
    max-height: 650px;
  }
  .contact-row input,
  .contact-row textarea {
    font-size: 0.85rem;
  }
}

@media screen and (min-width: 768px) {
  .contact-container {
    max-width: 450px;
    min-height: 700px;
    max-height: 700px;
  }
  .contact-row input,
  .contact-row textarea {
    font-size: 0.95rem;
  }
}
